@import '_var.scss';

/* оформление содержимого шаблона, которое находится под
header */
.block_numberfon {
 padding: 100px 0;
 background: linear-gradient(to right, #20172a 20%, #0f0917 80%, #0f0917 100%);
}

.block_numberfon h2 {
 display: none;
}

.block_numberbox {
 padding: 0;
 margin: 0;
 display: flex;
 justify-content: space-between;
}

.block_numberbox_boult,
.block_numberbox_num {
 width: 25%;
}

.block_numberbox_boult {
 position: relative;
}

.block_numberbox_boult img {
 position: absolute;
 top: -240px;
 left: -100px;
}

.numberbox_num {
 color: #fff;
 text-align: center;
 @include font-size(100px);
font-weight: 800;
}

.numberbox_num span {
 position: relative;
}

.numberbox_num span:before {
 left: -10px;
 bottom: 20px;
 position: absolute;
 content: '';
 width: pxrem(5px);
 height: pxrem(60px);
 background-color: $acsent_colir;
}

.numberbox_title {
 @include font-size(16px);
 color: $acsent_colir;
 text-transform: uppercase;
font-weight: 800;

}

.numberbox_descr {
 color: #a9a9a9;
}

.numberbox {
 padding: 0;
 margin: 0;
}

.block_feture {
 padding-top: 110px;
 background-color: #fafcfc;
}

.block_feture-title {
 text-align: center;
}

.block_feture-title h2 {
 @include font-size(30px);
 line-height: (22px / 30px);
 text-align: center;
 display: inline-block;
 text-transform: uppercase;
 font-family: "HelveticaNeueLTStd-Hv", sans-serif;
 font-weight: 200;
 color: #333740;
 margin-bottom: 26px;
}

.first_part {
 position: relative;
}

.first_part:before {
 right: -15px;
 bottom: -1px;
 position: absolute;
 content: '';
 height: 40px;
 width: 1px;
 background-color: $acsent_colir;
}

.supplement_facts .first_part:before {
 right: -19px;
}

.subscribe_block .first_part:before {
 right: -19px;
}

.block_feture-title h2 .first_part {
 @include font-size(30px);
 line-height: (22px / 30px);
 font-family: "HelveticaNeueLTStd-Hv", sans-serif;
 margin-right: 30px;
 font-weight: 800;
}

.block_feture-descr {
 color: #83888f;
 text-align: center;
 margin-bottom: 70px;
}

.feture_box {
 padding: 0;
 margin: 0;
 display: flex;
 justify-content: space-between;
 align-items: center;
}

.feture_box_items {
 width: 33%;
}

.feture_box_items_img {
 align-self: flex-end;
}

.feture_box_items_img img {
 display: block;
 width: 67%;
 height: auto;
 margin: 0 auto;
}

.f_items_right_title {
 @include font-size(20px);
 line-height: (24px / 20px);
 font-family: "HelveticaNeueLTStd-Hv", sans-serif;
 font-weight: 800;
 color: #333740;
 text-transform: uppercase;
 text-align: center;
 margin-bottom: 33px;
 position: relative;
}

.f_items_right_title:before {
 top: 0;
 right: 0;
 content: '';
 position: absolute;
 width: 5px;
 height: 35px;
 background-color: $acsent_colir;
}

.f_items_left_title {
 @include font-size(20px);
 line-height: (24px / 20px);
 font-family: "HelveticaNeueLTStd-Hv", sans-serif;
 font-weight: 800;
 color: #333740;
 text-transform: uppercase;
 text-align: center;
 margin-bottom: 33px;
 position: relative;
}

.f_items_left_title:before {
 top: 0;
 left: 0;
 content: '';
 position: absolute;
 width: 5px;
 height: 35px;
 background-color: $acsent_colir;
}

.f_items {
