// оформление карусели
.certifications_block .owl-theme .owl-nav.disabled+.owl-dots {
 margin-top: 40px;
}

.owl-theme .owl-dots .owl-dot span {
 cursor: pointer;
}

.owl-theme .owl-nav {
 margin-top: 0;
}

/*Изменяем внешний вид элементов управления (точки) */
.owl-theme .owl-dots .owl-dot span {
 width: 12px;
 height: 12px;
 border-radius: 50%;
 background-color: #342e3a;
}

.owl-theme .owl-dots .owl-dot.active span,
.owl-theme .owl-dots .owl-dot:hover span {
 background-color: $acsent_colir;
}

#carrousel .owl-carousel img {
 max-height: 100%;
 max-width: 100%;
 width: auto;
 height: auto;
 height: 300px;
 object-fit: cover;
 transition: all 0.2s ease;
 box-shadow: 12px 12px 10px -10px rgba(0, 0, 0, 0.25);
}

#carrousel .owl-carousel .owl-item {
 height: 300px;
 position: relative;
 transform: scale(0.7);
 -ms-transform: scale(0.7);
 transition: all .2s;
 -webkit-transform: all .2s;
 z-index: 1;
}

@media (max-width: 768px) {
 #carrousel .owl-carousel .owl-item {
  height: 270px;
 }
}

@media (max-width: 380px) {
 #carrousel .owl-carousel .owl-item {
  height: 200px;
 }
}

#carrousel .owl-carousel .owl-stage-outer {
 padding-bottom: 10px;
}

#carrousel .owl-item.big {
 transform: scale(1.0);
 z-index: 3;
}

#carrousel .owl-item.medium {
 transform: scale(0.7);
 z-index: 2;
}

#carrousel .owl-item.medium.mdright {
 position: relative;
 left: -30px;
}

@media (min-width: 768px) {
 #carrousel .owl-item.medium.mdright {
  left: 0px;
 }
}

#carrousel .owl-item.medium.mdleft {
 position: relative;
 right: -30px;
}

@media (min-width: 768px) {
 #carrousel .owl-item.medium.mdleft {
  right: 0px;
 }
}

#carrousel .owl-item.smallRight.active {
 transform: scale(0.7);
 -ms-transform: scale(0.7);
 position: relative;
 left: -30px;
}

#carrousel .owl-item.smallLeft.active {
 transform: scale(0.7);
 -ms-transform: scale(0.7);
 position: relative;
 right: -30px;
}

#carrousel .owl-nav {
 position: absolute;
 top: 30%;
 width: 100%;
}

#carrousel .owl-nav .owl-prev,
#carrousel .owl-nav .owl-next {
 position: absolute;
 top: 50%;
 border: 3px solid #c6cfd2;
 transform: translateY(50%);
 width: 60px;
 height: 60px;
 line-height: 60px;
 border-radius: 50%;
 background-color: transparent;
 color: transparent;
 background-repeat: no-repeat;
 padding: 0;
 text-align: center;
 // background-size: 60px 60px;
}

#carrousel .owl-nav .owl-prev {
 left: -60px;
 transform: translateX(-100%);
}

#carrousel .owl-nav .owl-next {
 right: -60px;
 transform: translateX(100%);
}

#carrousel .owl-nav .owl-prev:before {
 position: absolute;
 content: "\f060";
 font-family: "FontAwesome";
 text-align: center;
 line-height: 60px;
 font-weight: normal;
 @include font-size(26px);
 color: #c6cfd2;
}

#carrousel .owl-nav .owl-next:before {
 position: absolute;
 content: "\f061";
 font-family: "FontAwesome";
 text-align: center;
 font-weight: normal;
 @include font-size(26px);
 color: #c6cfd2;
}

#carrousel .owl-theme .owl-dots {
 padding-top: 55px;
}

